@keyframes shake {
  0% {
    transform: translate(1px, 1px) rotate(0deg);
  }
  10% {
    transform: translate(-1px, -2px) rotate(-1deg);
  }
  20% {
    transform: translate(-3px, 0px) rotate(1deg);
  }
  30% {
    transform: translate(3px, 2px) rotate(0deg);
  }
  40% {
    transform: translate(1px, -1px) rotate(1deg);
  }
  50% {
    transform: translate(-1px, 2px) rotate(-1deg);
  }
  60% {
    transform: translate(-3px, 1px) rotate(0deg);
  }
  70% {
    transform: translate(3px, 1px) rotate(-1deg);
  }
  80% {
    transform: translate(-1px, -1px) rotate(1deg);
  }
  90% {
    transform: translate(1px, 2px) rotate(0deg);
  }
  100% {
    transform: translate(1px, -2px) rotate(-1deg);
  }
}

@keyframes ellipsis {
  to {
    width: .9em;
  }
}

/* components */
::selection {
  background-color: #579c87;
  color: #ffcb4f;
  -webkit-text-fill-color: #ffcb4f;
}

body {
  font-family: 'Roboto', sans-serif;
  background-color: #f8f8f8;
}

a {
  color: #ffcb4f;
  transition: all .3s ease-in;
}

img {
  width: 100%;
  height: auto;
  vertical-align: middle;
}

.container {
  max-width: 1140px;
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin: 2rem auto 8rem;
}

.row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  margin-right: -15px;
  margin-left: -15px;
}

.col {
  flex: 0 0 100%;
  max-width: 100%;
  position: relative;
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  padding-bottom: 30px;
}

@media (min-width: 768px) {
  .col {
    flex: 0 0 50%;
    max-width: 50%;
  }
}

.col-12 {
  flex: 0 0 100%;
  max-width: 100%;
  position: relative;
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  padding-bottom: 30px;
}

.flex {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
  align-content: stretch;
  align-items: flex-start;
  margin: 0 0 2rem;
}

.button {
  background-color: #579c87;
  padding: .8rem 1.2rem;
  border-radius: 4px;
  font-weight: bold;
}

.button:hover {
  background-color: #74b29f;
  color: #ffd369;
}

.overlay {
  opacity: 0;
  visibility: hidden;
  position: fixed;
  display: block;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 9;
  background-color: rgba(255, 255, 255, 0.6);
  backdrop-filter: blur(8px);
}

.overlay.active {
  opacity: 1;
  visibility: visible;
  transition: all .2s ease-in;
}

.soon {
  text-align: center;
  padding: .8rem 1.5rem;
}

.soon:after {
  position: absolute;
  content: "\2026";
  animation: ellipsis steps(4, end) 0.9s infinite;
  width: 0;
  overflow: hidden;
  display: inline-block;
  vertical-align: bottom;
}

.menus .menu a {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 700px;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.08);
  padding: .5rem;
  overflow: hidden;
}

.menus .menu a:hover h2 {
  font-size: 2.3rem;
}

.menus .menu a:hover h2 span {
  font-size: 2.7rem;
}

.menus .menu a img {
  display: block;
  width: 80px;
  margin: 0 auto .5rem;
  box-shadow: none;
}

.menus .menu a h2 {
  font-size: 2.2rem;
  text-align: center;
  transition: all .3s ease-in-out 0s;
}

.menus .menu a h2 span {
  display: block;
  font-size: 2.6rem;
  margin-top: 2rem;
  transition: all .3s ease-in-out 0s;
}

header {
  position: relative;
  background-color: #579c87;
  padding: 3rem 0;
}

header .logo {
  display: block;
  max-width: 350px;
  margin: 0 auto;
  padding: 1.5rem;
}

/* mobile */
@media (max-width: 767px) {
  .nav {
    z-index: 99;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    background-color: #457b6b;
    width: 270px;
    padding: 2rem 0;
    box-shadow: 1px 0px 3px rgba(0, 0, 0, 0.2);
    transform: translate3d(-270px, 0, 0);
    transition: transform .2s ease-in;
  }
  .nav.active {
    transform: translate3d(0, 0, 0);
  }
  .nav ul li .nav-link {
    display: block;
    padding: 1rem 1.5rem;
    font-weight: bold;
  }
  .nav ul li .nav-link:hover {
    background-color: #4e8c79;
    color: #ffd369;
  }
}

#mobile-menu-wrapper.sticky {
  z-index: 9;
  background-color: #579c87;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 50px;
}

#mobile-menu-wrapper.sticky #mobile-menu {
  top: .35rem;
}

#mobile-menu-wrapper #mobile-menu {
  position: absolute;
  right: 1rem;
}

#mobile-menu-wrapper #mobile-menu span {
  font-size: 2.5rem;
}

/* tablet */
@media (min-width: 768px) {
  .nav ul {
    max-width: 960px;
    margin: 0 auto;
    text-transform: uppercase;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-around;
    align-content: flex-start;
    align-items: flex-start;
  }
  .nav ul li .nav-link {
    display: block;
    text-align: center;
    padding: 1rem;
    font-weight: bold;
  }
  .nav ul li .nav-link:hover {
    background-color: #4e8c79;
    color: #ffd369;
    border-radius: 8px;
  }
  #mobile-menu {
    display: none;
  }
}

.menus {
  margin: 1rem 0;
}

.menus .menu h1 {
  font-size: 2.5rem;
  text-align: center;
  margin-bottom: 2rem;
}

.one-menu {
  background-color: #fcdfdb;
  padding: 2rem 1rem;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.08);
}

.one-menu h2 {
  position: relative;
  font-size: 1.7rem;
  text-align: center;
  margin-bottom: 3rem;
}

.one-menu h2 span {
  position: relative;
}

.one-menu h2 span:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: #000;
  visibility: hidden;
  transform: scaleX(0);
  transition: all 0.3s ease-in-out 0s;
}

.one-menu h2 span:hover:before, .one-menu h2 span:hover:after {
  visibility: visible;
  transform: scaleX(1);
}

.one-menu ul {
  margin-bottom: 5rem;
}

.one-menu ul li {
  position: relative;
  font-size: 1.2rem;
  letter-spacing: 1px;
  margin: 0 auto 1.5rem;
  text-align: justify;
}

@media (min-width: 768px) {
  .one-menu ul li {
    text-align: center;
  }
}

.one-menu ul li span {
  display: block;
  text-align: center;
  font-weight: bold;
  margin-top: .5rem;
}

.one-menu .header-menu {
  margin: 0 auto 2rem;
  text-align: center;
}

.one-menu .header-menu h1 {
  font-size: 3rem;
}

.one-menu .header-menu img {
  width: 120px;
  margin-bottom: 1rem;
}

.one-menu .footer-menu {
  margin: 0 auto 2rem;
  text-align: center;
}

.one-menu .footer-menu img {
  width: 80px;
}

.one-menu .footer-menu span {
  font-size: 1.7rem;
  vertical-align: middle;
}

@media (min-width: 991px) {
  .one-menu {
    padding: 3rem 4rem;
  }
  .one-menu h2:before, .one-menu h2:after {
    content: "";
    display: block;
    background-image: url("../img/border-01.png");
    background-position: center center;
    background-repeat: no-repeat;
    background-size: contain;
    width: 30%;
    height: auto;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
  }
  .one-menu h2:after {
    background-image: url("../img/border-02.png");
    right: 0;
    left: initial;
  }
  .one-menu ul li {
    padding-right: 8rem;
    text-align: justify;
  }
  .one-menu ul li span {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    margin: 0;
  }
  .menu02 .one-menu h2::before {
    background-image: url("../img/border-03.png");
  }
  .menu02 .one-menu .section-menu-02 h2::before {
    background-image: url("../img/border-03a.png");
  }
  .menu02 .one-menu .section-menu-02 h2::after {
    background-image: url("../img/border-04a.png");
  }
  .menu02 .one-menu .section-menu-03 h2::before {
    background-image: url("../img/border-04b.png");
  }
  .menu02 .one-menu .section-menu-03 h2::after {
    background-image: url("../img/border-03b.png");
  }
  .menu02 .one-menu h2::after {
    background-image: url("../img/border-04.png");
  }
  .menu03 .one-menu h2::before {
    background-image: url("../img/border-05.png");
  }
  .menu03 .one-menu h2::after {
    background-image: url("../img/border-06.png");
  }
  .menu04 .one-menu h2::before {
    background-image: url("../img/border-07.png");
  }
  .menu04 .one-menu h2::after {
    background-image: url("../img/border-08.png");
  }
}

